* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Monserrat', sans-serif;
}

body {
    background-color: #0B2434;
}

main {
    height: 80vh;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
}

button {
    margin: 8px 6px;
    padding: 10px 14px;
    border-radius: 4px;
    border: 1px solid #ccc;
    box-shadow: 0 0 4px rgba(34, 34, 34, 0.25);
}

button:hover {
    cursor: pointer;
}

.title {
    text-align: center;
    color: white;
    margin: 12px;
}

.warning {
    color: #113d58;
    font-weight: bold;
}

.app-sec {
    padding: 12px;
    border-radius: 8px;
    display: flex;
    align-items: center;
    flex-direction: column;
    border: 1px solid #ccc;
    background-color: #f3f2e6;
}

.stats {
    padding-bottom: 8px;
    width: 95%;
    display: flex;
    justify-content: space-between;
}

.dices {
    width: 275px;
    margin: 12px;
    padding: 8px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.hold {
    border: 1px solid rgb(233, 244, 253);
    background-color: rgb(233, 244, 253);
    box-shadow: 0 0 5px rgb(0, 0, 0);
}

.end-game {
    display: flex;
    align-items: center;
    flex-direction: column;
    padding: 12px 48px;
    line-height: 24px;
}

.exit-btn {
    padding: 7px 10px;
}

.credit {
    color: #615f52;
    font-size: 12px;
}

.socials {
    display: flex;
    justify-content: space-around;
}

.socials > a {
    color: white;
}

.socials > a:hover {
    color: rgb(255, 248, 200);
}